.artist-page {
    display: grid;
    grid-template-columns: 230px 50px 400px 1fr 75px;
    grid-template-rows: 50px 75px 60px 40px 100px 210px 30px 50px 400px 25px;
    overflow: scroll;
    width: 100vw;
    height: 100vh;
    grid-template-areas:
    " sidebar    .             .              .        .   "
    " sidebar    .        artist-name   artist-name    .   "
    " sidebar    .         play-button    play-button  .   "
    " sidebar    .         nav-links       nav-links   .   "
    " sidebar    .        album-header        .        .   "
    " sidebar    .           albums           .        .   "
    " sidebar    .             .              .        .   "
    " sidebar    .        track-header        .        .   "
    " sidebar    .         tracks           tracks     .   "
    " sidebar    .             .              .        .   ";
    background-image: linear-gradient(to right bottom, rgb(92, 92, 61), rgb(0, 0, 0)), linear-gradient(transparent, rgb(0, 0, 0) 70%);
    align-items: center;
}

.artist-page__name {
    grid-area: artist-name;
    justify-self: center;
    font-size: 48px;
    line-height: 56px;
    letter-spacing: -.005em;
}

.play-button {
    grid-area: play-button;
    padding: 11px 25px 9px;
    font-weight: 300;
    background-color: #1db954;
    cursor: pointer;
    width: 160px;
    border-radius: 50px;
    border: none;
    text-transform: uppercase;
    font-family: 'Rubik', Helvetica, Arial, sans-serif;
    font-size: 12px;
    letter-spacing: 2px;
    color: #fff;
    transition: 0.2s ease-in-out;
    justify-self: center;
}

.play-button:hover {
    transform: scale(1.1);
    background-color: rgb(52, 215, 102);
}

.artist__nav-links {
    display: flex;
    justify-self: center;
    grid-area: nav-links;
}

.artist__overview, .artist__about {
    opacity: .6;
    transition: opacity .2s linear;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: .16em;
    margin-left: 40px;
    position: relative;
    left: -20px;
    cursor: pointer;
}


.artist__overview:hover, .artist__about:hover {
    opacity: 1;
}

.artist__albums-header {
    grid-area: album-header;
}

.artist__albums {
    grid-area: albums;
    display: flex;
}

.artist__album {
    display: flex;
    flex-direction: column;
    width: 180px;
    align-items: center;
    margin-right: 25px;
}

.artist__album-img-and-title {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.artist__album-img {
    width: 180px;
    height: 180px;
    position: relative;
}

.artist__album-img-link {
    height: 180px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.artist__album-img-and-title:hover .artist__play-button, .artist__album-img-and-title:hover .artist-page__black-layer
{
    opacity: 1;
}

.artist-page__black-layer {
    opacity: 0;
    position: absolute;
    width: 180px;
    height: 180px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 1;
    transition: opacity ease 0.2s;
    cursor: pointer;
}


.artist__album-title, .artist__album-artist {
    margin-top: 10px;
    margin-bottom: 0;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: .015em;
    cursor: pointer;
    text-decoration: none;
    color: white;
}

.artist__album-title {
    text-align: center;
}

.artist__album-artist {
    opacity: 0.6;
    margin-top: 0;
    transition: opacity 0.2s ease, border-bottom 0.2s ease;
    border-bottom: solid 1px transparent;
}

.artist__album-artist:hover {
    opacity: 1;
    border-bottom: solid 1px white;
}

.artist__tracks {
    grid-area: tracks;
    overflow: scroll;
    height: 400px;
}

.artist__tracks-header {
    grid-area: track-header;
}

.artist__tracks-header, .artist__albums-header {
    font-size: 36px;
    line-height: 44px;
    letter-spacing: -.005em;
    position: relative;
    left: -20px;
}






.audio-player {
    height: 103px;
    width: 100vw;
    position: fixed;
    bottom: 0;
    display: grid;
    grid-auto-flow: column; 
    align-items: center;
    background-color: #282828;
    grid-template-columns: 20px 200px 1fr 300px 1fr 20px 20px 150px 20px;
    grid-template-areas: 
     ' .   song-info   .    controls      .     queue   mute  volume-slider   .  ' ;
}

.audio-player__album-img {
    width: 56px;
    height: 56px;
    grid-area: img;
}

.audio-player__song-info {
    display: grid;
    grid-area: song-info;
    grid-template-columns: 60px 15px 1fr;
    grid-template-rows: 50px 50px;
    grid-template-areas: 
     ' img   .       song     ' 
     ' img   .   artist-name ';
     align-items: center;
}

.audio-player__song-title {
    grid-area: song;
    font-size: 14px;
    font-weight: 300;
    line-height: 1.3;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    align-self: flex-end;
    margin: 0;
}

.audio-player__artist-name {
    grid-area: artist-name;
    font-size: 12px;
    color: rgba(255,255,255,0.6);
    font-weight: 300;
    line-height: 1.3;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    align-self: flex-start;
    margin-top: 3px;
}


.audio-player__volume-slider {
    cursor: pointer;
}

.audio-player__controls {
    grid-area: controls;
    justify-self: center;
}

.audio-player__play-queue {
    grid-area: queue;
}

.audio-player__mute-sound {
    grid-area: mute;
}

.audio-player__volume-slider {
    grid-area: volume-slider;
}

.svg {
    /* width: 100%; */
    height: 45px;
    width: 45px;
    stroke: rgba(255,255,255,0.6);
    fill: transparent;
    stroke-width: 6;
    transition: stroke 0.3s ease;
    cursor: pointer;
}

.svg path, .svg rect {
    fill: rgba(255,255,255,0.6);
    transition: fill 0.2s ease;
}

.svg:hover path, .svg:hover rect {
    fill: rgba(255,255,255,1);
}

.player:hover, .arrows:hover, .play-pause:hover {
    stroke: rgba(255,255,255,1);
}

.play-pause {
    transition: transform 0.3s ease;
}

.play-pause:hover {
    transform: scale(1.2);
}

.pause {
    display: none;
}

.play {

}

.audio-player__current-time {
    display: flex;
    align-items: center;
    position: relative;
    top: -10px;
}

.audio-player__time {
    font-size: 12px;
    width: 30px;
    color: #919496;
    font-weight: 300;
    margin-right: 5px;
    margin-left: 5px;
}

.audio-player__time-slider {
    width: 100%;
    padding: 0;
    margin: 0;
    /* background-color: transparent; */
    /* background-color: green; */
    background-image: -webkit-gradient(linear, 50% 0%, 50% 100%, color-stop(0%, #404040), color-stop(100%, #404040));
    -webkit-appearance: none;
    border: none;
    height: 4px;
    border-radius: 4px;
    cursor: pointer;
}

.audio-player__time-slider:focus, .audio-player__time-slider:active {
    outline: none;
}

.audio-player__time-slider::-webkit-slider-thumb {
    -webkit-appearance: none;
     width: 14px;
    height: 14px;
    border-radius: 50%;
    background: #ddd;
    cursor: pointer;
    transition: opacity .2s ease-in-out;
    opacity: 0;
}

.audio-player__time-slider:hover::-webkit-slider-thumb {
    opacity: 1;
}

.audio-player__time-slider::-webkit-progress-value {
  background-color: blue;
}



